<template>
	<div class="AreaPop">
		<div  class="AreaPopCon">
			<div class="title">
				<input type="text" name="" placeholder="搜索国家/地区">
				<span><i class="icon iconfont icon-cha"></i></span>
			</div>
			<div class="meun">
				<ul>
					<li><span class="">中国大陆</span><span>+86</span></li>
					<li><span>香港</span><span>+852</span></li>
					<li><span>澳门</span><span>+853</span></li>
					<li><span>美国</span><span>+1</span></li>
					<li><span>英国</span><span>+44</span></li>
					<li><span>加拿大</span><span>+1</span></li>
					<li><span>澳大利亚</span><span>+61</span></li>
					<li><span>法国</span><span>+33</span></li>
				</ul>
			</div>
		</div>
	</div>
</template>
<script>
	export default{
		data() {
		    return {
			    
			}
		},
		// props:['titleCom','titleComTow','icon']
		props:{
			titleCom:'',
			titleComTow:'',
			icon:{
				default:1
			}
		}
	}
</script>
<style scope>
	/*弹窗样式*/
	.AreaPop .AreaPopCon .title{
		position: relative;
		width: 100%;
		text-align: left;
		height: 1.2rem;
		padding-left: 0.4rem;
		border-bottom: 1px solid #ececec;
	}
	.AreaPop .AreaPopCon .title input{
		outline: none;
		border: none;
		font-size: 0.3rem;
		height: 1.1rem;
		line-height: 1.1rem;
		vertical-align: top;
	}
	.AreaPop .AreaPopCon .title .icon,
	.AreaPop .AreaPopCon .title span{
		position: absolute;
		right:  0;
		height: 1.1rem;
		line-height: 1.1rem;
		font-size: 0.4rem;
		
	}
	.AreaPop .AreaPopCon .title span{
		margin: 0 0.4rem;
		border-left: 1px solid #f3f3f3;
		width: 1rem;
	}
</style>


